	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"","date":"13 Jul 2000","slug":"/2000-07-13-auslaender/","tags":"Y2000"},"html":"<p>German has word to describe all things foreign that cannot really be translated directly into English. <i>Ausland.</i> Out of land. Out of space, out of mind, out of time. There, as opposed to here. Someplace else, someplace different.</p>\n<p>That makes me, since I'm not German, an <i>Ausländer.</i> Being German is somethi to which you are born and cannot become (as opposed to being American, which you can become if you stay in the US long enough and watch enough televsion), so as long as I am in Germany I will always be an <i>Ausländer</i>.</p>\n<p>That's not so bad. You pay the same taxes whether you're German or not, and not being able to vote gives me a right to complain about politicians. Despite what some Germans think about themselves, that Germans have something against <i>Ausländer</i>, I've never felt particularly disadvantaged.</p>\n<p>That's not to say that I don't get any strange reactions. Some people regard an American who can speak German with the same awe as they would regard a talking pig. ('Well, what do you think about that. Now I've seen everything.') Others think that because they've seen so many American movies and heard so much American music, that they know all about me and where I come from, even though they really don't have a clue. (I come from Minnesota, so maybe they need to watch 'Fargo' a few times. America is more complicated that it appears. Uff-da!)</p>\n<p>Now, of course, my skin is white, I don't live in a ghetto, and I speak German. (And if I try real hard, I can hide my American accent.) That is, at first glance I'm no different than most Germans. Those <i>Ausländer</i> who look and sound different have a lot more problems than I. But that has nothing to do with Germans being particularly anti-<i>Ausländer</i>. That's just plain old ugly racism, something I'm all to familiar with from home. But it's no more or no less ugly in Germany than it is everywhere else. Racism is a human problem and not a particularly German problem.</p>\n<hr />\nTyler Pruett <a href=\"http://shanson.editthispage.com/discuss/msgReader$287\">suggests</a> the freeware program <a href=\"http://softwarecenter.net/irfanview/\">Irfanview</a> to resize graphics.</p>"},"next":{"fields":{"title":"Do you need to be an expert?","date":"11 Jul 2000","slug":"/2000-07-11-do-you-need-to-be-an-expert/"}},"previous":{"fields":{"title":"House Warming","date":"15 Jul 2000","slug":"/2000-07-15-house-warming/"}}},{"node":{"fields":{"title":"Do you need to be an expert?","date":"11 Jul 2000","slug":"/2000-07-11-do-you-need-to-be-an-expert/","tags":"Y2000"},"html":"<p>For the past few weeks, I've been searching for a way to make it easy for Mama to copy pictures from our Nikon digital camera to the Internet. Now, Mama's smart, but she isn't a computer expert and she doesn't want to become one. She has better things to do in her spare time. (She doesn't want or need to become a geek... she married one, after all.)</p>\n<p>What she wants is to be able to copy JPEG files from a Smart Card (she has a USB drive for the card), reduce the picture size from 1600x1200 to 640 or 350 wide (depending on where she is uploading), then automatically upload the files. That's all she wants to do, and she doesn't want to have to learn (or pay for) PhotoShop or PaintShop or whatever to do it.</p>\n<p>Her geek husband can do this for her in about 20 lines of <a href=\"http://www.perl.com\">perl</a>. (\"Frontier\" can do everything but the picture resizing, I suspect.) That is, if he can be bothered to install perl for her under windows. And since it seems there is no commericial program out there that can do this for her, I guess that's what he'll have to do.</p>"},"next":{"fields":{"title":"At Your Side","date":"09 Jul 2000","slug":"/2000-07-09-at-your-side/"}},"previous":{"fields":{"title":"","date":"13 Jul 2000","slug":"/2000-07-13-auslaender/"}}},{"node":{"fields":{"title":"At Your Side","date":"09 Jul 2000","slug":"/2000-07-09-at-your-side/","tags":"Y2000"},"html":"<p><a href=\"http://www.thecorrs.org/disco/lyrics/lyrics_a.html#ays\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/07/xmmscorrs.jpg\" height=\"116\" width=\"275\" border=\"0\" alt=\"xmmscorrs.jpg: \" /></a></p>\n<p>Christopher seems to enjoy 'dancing' (which at his age means being held by an adult who is dancing) to music on the radio. Today we overcame a small crying phase with help of <a href=\"http://www.thecorrs.org/disco/lyrics/lyrics_a.html#ays\">The Corrs</a>.</p>\n<p>He also likes the classic 'Surfin' Bird' because he can sing along. 'Pa-pa-pa-pa-pa-pa-pa Mow-mow, papa-ooma-mow-mow!'</p>"},"next":{"fields":{"title":"Tel Aviv","date":"08 Jul 2000","slug":"/2000-07-08-tel-aviv/"}},"previous":{"fields":{"title":"Do you need to be an expert?","date":"11 Jul 2000","slug":"/2000-07-11-do-you-need-to-be-an-expert/"}}}],"pathPrefix":"","first":false,"last":false,"index":1053,"pageCount":1101}}

